import React, { Component } from "react";
import styled from "styled-components";
import { data } from "../../pages/data.json";

const Div = styled.div`
  background: #f2f7fa;
`;

const Content = styled.div`
  max-width: 980px;
  padding: 35px 15px;
  margin: 0 auto;
  .more-title {
    border-bottom: 1px solid #e9e8e7;
    padding-bottom: 10px;
  }
`;

const More = styled.div`
  float: left;
  width: 33.3%;
  padding-right: 20px;
  margin: 35px 0;
  &:last-child {
    padding-right: 0;
  }
  .more-subtitle {
    font-size: 13px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 12px;
  }
  h3 {
    font-family: "Galano";
    font-size: 28px;
  }
`;

const Author = styled.div``;

const Image = styled.div`
  width: 100%;
  height: 200px;
  background: ${props =>
    `url("/static/img/work/main/${props.img}.png") no-repeat center`};
  background-size: cover;
`;

class MoreRelation extends Component {
  state = {
    relationArticle: 0
  };
  componentDidMount = () => {
    const length = data.length;
    const chooseArticle = this.getRandom(length);
    this.setState({
      relationArticle: chooseArticle
    });
  };

  getRandom = number => {
    return Math.floor(Math.random() * number);
  };

  render() {
    const article = data[this.state.relationArticle];
    return (
      <Div>
        <Content>
          <h2 className="more-title">更多相關的文章</h2>
          <div>
            <More>
              <div className="more-subtitle">{article.subtitle}</div>
              <Image img={article.id} />
              <h3>{article.title}</h3>
              <Author>123</Author>
            </More>
            <More>
              <div className="more-subtitle">{article.subtitle}</div>
              <Image img={article.id} />
              <h3>{article.title}</h3>
              <Author>123</Author>
            </More>
            <More>
              <div className="more-subtitle">{article.subtitle}</div>
              <Image img={article.id} />
              <h3>{article.title}</h3>
              <Author>123</Author>
            </More>
          </div>
          <div style={{ clear: "both" }} />
        </Content>
      </Div>
    );
  }
}

export default MoreRelation;
